.wrapper {
  padding: 20px 20px;
  background-color: rgb(208, 208, 208);
}
.title {
  font-size: 32px;
  text-transform: uppercase;
  line-height: 45px;
  text-align: center;
}
.imageWrapper {
  display: none;
}
.listWrapper {
  display: flex;
  flex-direction: column;
}
.item {
  padding: 10px 0;
  transition: background-color 0.6s;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  border-bottom: 2px solid rgb(34, 36, 49);
}
.active {
  background-color: rgb(34, 36, 49);
}
.active .itemTitle {
  color: white;
}
.active .text {
  color: white;
  opacity: 1;
}
.itemTitle {
  font-size: 20px;
  line-height: 28px;
  font-weight: 900;
}
.text {
  font-size: 14px;
  opacity: 0;
  line-height: 1.4;
  font-weight: 900;
}

@media (min-width: 768px) {
  .wrapper {
    padding: 60px 30px;
  }
  .title {
    font-size: 50px;
    text-transform: uppercase;
    text-align: start;
  }
  .title::after {
    content: "↘";
    font-size: 60px;
  }

  .item {
    padding: 20px 10px;
    flex-direction: row;
  }
}

@media (min-width: 1024px) {
  .wrapper {
    padding: 70px 50px;
  }
  .container {
    display: flex;
    gap: 40px;
    align-items: flex-start;
  }
  .title {
    padding-bottom: 18px;
  }

  .imageWrapper {
    display: block;
    position: relative;
    flex: 1;
    overflow: hidden;
    max-width: 320px;
    height: 400px;
  }
  .image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    transform: scale(1);
    transition: opacity 0.6s ease-in-out, transform 0.6s ease-in-out;
  }

  .show {
    opacity: 1;
    transform: scale(1.03);
  }
  .listWrapper {
    flex: 1;
  }
  .item {
    cursor: pointer;
    align-items: center;
    flex-direction: column;
  }

  .item:hover {
    background-color: rgb(34, 36, 49);
  }
  .itemTitle {
    flex: 2;
    font-size: 26px;
  }

  .text {
    flex: 4;
    font-size: 16px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    padding: 70px 60px;
  }
  .imageWrapper {
    max-width: 400px;
    height: 450px;
  }
  .item {
    flex-direction: row;
  }
}
